body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
 background-image: linear-gradient( rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2) ),url(/static/media/intback.71c2bc9f.jpg);
 background-repeat: no-repeat;
 background-size: cover;
 background-attachment: fixed;
 height: auto;
}


#AboutContent {
    justify-content: center;
}

#LogoImg {
    width: 100%;
    height: 100%;
    transition: 300ms;
    -webkit-filter: drop-shadow(0 0 0.2rem white);
            filter: drop-shadow(0 0 0.2rem white);
}
#LogoImg:hover {
    -webkit-filter: drop-shadow(0 0 0.75rem white);
            filter: drop-shadow(0 0 0.75rem white);
}

#PassesButton {
    -webkit-filter: drop-shadow(0 0 0.5rem white);
            filter: drop-shadow(0 0 0.5rem white);
    -webkit-transform: scale(1);
            transform: scale(1);
    transition: 200ms;
   
}
#PassesButton:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    -webkit-filter: drop-shadow(0 0 2rem white);
            filter: drop-shadow(0 0 2rem white);
    border-color:white;
    border-width: 1px;
    color: cyan;
}

.parent {
    display: grid;
    width:100%;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto;
    grid-column-gap: 0px;
    grid-row-gap: 0px;
    }
    
    .div1 { 
        /* background-color: aqua; */
    } 
    .div2 { 
        padding-left: 10%;
        padding-right: 10%;
        text-align: justify;
        color: white;
        /* background-color: aquamarine; */
        font-family: 'Black Ops One', cursive;
    }
#timer {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-template-rows: 1fr 4fr;
        grid-column-gap: 0px;
        grid-row-gap: 0px;
        text-align: center;
}
#days {
    font-size: 100px;
    color:white;
    grid-area: 2 / 1 / 3 / 2;
    font-family: 'Black Ops One', cursive;

  }
  #hours {
    font-size: 100px;
    color: white;
    grid-area: 2 / 2 / 3 / 3;
    font-family: 'Black Ops One', cursive;

  }
  #minutes {
    font-size: 100px;
    color: white;
    grid-area: 2 / 3 / 3 / 4;
    font-family: 'Black Ops One', cursive;

  }
  #seconds {
    font-size: 100px;
    color: white;
    grid-area: 2 / 4 / 3 / 5;
    font-family: 'Black Ops One', cursive;

  }

.labelDays { font-size: 2rem;
    color: white;
    font-family: 'Black Ops One', cursive;
    grid-area: 1 / 1 / 2 / 2; }
.labelHours { 
    font-size: 2rem;
    color: white;
    font-family: 'Black Ops One', cursive;grid-area: 1 / 2 / 2 / 3; }
.labelMins { 
    font-size: 2rem;
    color: white;
    font-family: 'Black Ops One', cursive;grid-area: 1 / 3 / 2 / 4; }
.labelSecs { 
    font-size: 2rem;
    color: white;
    font-family: 'Black Ops One', cursive;grid-area: 1 / 4 / 2 / 5; }
.eventLab {
    font-family: 'Black Ops One', cursive;
    color: white;
    font-size: 6rem;
}
.carousel-inner {
    font-family: 'Black Ops One', cursive;
    color: white;
}
